import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Index from '@/components/index/Index'
import Movies from '@/components/movies/Movies'
import Cinema from '@/components/cinema/Cinema'
import Show from '@/components/show/Show'
import List from '@/components/list/List'
import HotSpot from '@/components/hotSpot/HotSpot'
import MovieTicket from '@/components/movies/MovieTicket.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/',
          name: 'Index',
          component: Index
        },
        {
          path: '/index',
          name: 'Index',
          component: Index
        },
        {
          path: '/movies',
          name: 'Movies',
          component: Movies,
          children: [

          ]
        },
        {
          path: '/cinema',
          name: 'Cinema',
          component: Cinema
        },
        {
          path: '/show',
          name: 'Show',
          component: Show
        },
        {
          path: '/list',
          name: 'List',
          component: List
        },
        {
          path: '/hotSpot',
          name: 'HotSpot',
          component: HotSpot
        },
        {
          path: '/mticket',
          name: 'MovieTicket',
          component: MovieTicket
        }
      ]
    }
  ]
})



