<template>
  <div class="mt-content clearfix">
    <div class="left-content left">
      <div class="header-guider">
        <el-breadcrumb separator=">">
          <el-breadcrumb-item :to="{ path: '/movies' }">电影</el-breadcrumb-item>
          <el-breadcrumb-item>购票</el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <div class="ticket-item clearfix">
        <div class="movie-detail">
          <div class="left">
            <img :src="moviePic">
          </div>
          <div class="left introduce">
            <h3>扫毒2天地对决</h3>
            <h3>The White Storm 2</h3>
            <p>剧情,动作,悬疑,犯罪</p>
            <p><span>中国香港,中国大陆</span> / <span>100</span>分钟</p>
          </div>
        </div>
      </div>
      <div class="word_break_line">
        毒品市场维持四分天下的格局已久，但自从地藏（古天乐 饰）与墨西哥大毒枭跨境合作扩展势力，再加上一连串黑吃黑的动作，毒界变得风声鹤唳。另一方面，因儿时亲眼目睹父亲被毒品所毁而嫉毒如仇的慈善家兼金融巨子余顺天（刘德华 饰），正悬赏一亿歼灭香港最大毒贩，此举在社会上引起轩然大波。警员林正风（苗侨伟 饰）本致力搜证拘捕地藏，毒贩却因巨额悬赏导致人身安全受威胁，他在执行保护毒贩的任务时深感无奈，但又被余顺天的出价所诱惑，陷入黑白正邪的心理挣扎。原来，余顺天和地藏有不可告人的同门关系，一场天地对决一触即发。
      </div>
    </div>
    <div class="right-content left">
      右侧可以选择的影院
    </div>
  </div>
</template>

<script>
import ImageConst from '@/components/constants/ImageConst'

export default {
  name: 'MovieTicket',
  data () {
    return {
      moviePic: ImageConst.moviePic
    }
  }
}
</script>

<style scoped>
.mt-content {
  margin: 0 2rem 0 2rem;
}
.left-content {
  width: 35%;
}
.el-breadcrumb {
  margin-top: 15px;
  font-size: 24px!important;
}
.ticket-item {
  margin-top: 1rem;
}
.movie-detail img {
  width: 250px;
  height: 350px;
}
.introduce {
  margin-left: 20px;
  text-align: left;
}
.word_break_line {
  text-align: left;
}
</style>



